import React, { useCallback, useEffect, useState } from 'react';
import {
  Button, Container, Grid, Loader, TextArea
} from 'semantic-ui-react';

import styles from './App.module.css';

import 'semantic-ui-css/semantic.min.css';

function getRandomInt(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

const App = () => {
  const [dictionary, setDictionary] = useState(null);
  const [codes, setCodes] = useState([]);

  const handleGenerateClick = useCallback(() => {
    const usedWords = [];
    const nextCodes = [];

    while (nextCodes.length < 50) {
      const word = dictionary[Math.floor(Math.random() * dictionary.length)];
      if (usedWords.includes(word)) {
        continue;
      }

      usedWords.push(word);
      nextCodes.push(`${word}${getRandomInt(10, 999)}`);
    }

    setCodes(nextCodes);
  }, [dictionary]);

  useEffect(() => {
    (async () => {
      setDictionary((await import('./dictionary.json')).default);
    })();
  }, []);

  if (!dictionary) {
    return <Loader active>Загружаю базу...</Loader>;
  }

  return (
    <Container>
      <Grid padded>
        <Grid.Row>
          <Grid.Column>
            <Button fluid content="Сгенерировать" onClick={handleGenerateClick} />
          </Grid.Column>
        </Grid.Row>
        {codes.length > 0 && (
          <Grid.Row>
            <Grid.Column>
              <TextArea value={codes.join('\n')} rows={codes.length} className={styles.textarea} />
            </Grid.Column>
          </Grid.Row>
        )}
      </Grid>
    </Container>
  );
};

export default App;
